<template>
  <div>
    <crud-edit
      button
      :fetch-data="{
        url: '/api/posts/:param',
        param: $route.params.slug,
        data: 'post'
      }"
      :edit-data="{
        url: '/api/posts/:param',
        param: '_id',
        formData: true,
        successRedirect: '/dashboard/blog/posts'
      }"
    >
      <template v-slot:crud-form="{ item: post }">
        <PostForm :post="post" :edit="true" />
      </template>
    </crud-edit>
  </div>
</template>

<script>
export default {
  middleware: ['pagePermission'],
  components: {
    CrudEdit: () => import('@/components/crud/CrudEdit'),
    PostForm: () => import('@/components/forms/blog/PostForm')
  },
  async created() {
    await this.$store.dispatch('post/fetchCategories', this.$axios)
    await this.$store.dispatch('post/fetchTags', this.$axios)
  }
}
</script>

